.btn{
  .heading-content &{
    margin-right: 27px;
    margin-bottom: 10px;
    padding: 10px 25px;
    text-decoration: none;
    text-transform: uppercase;
  }
}
.btn-default{
  border-color: @tzpp_border-color;
  .heading-content &{
    color: white;
    background: none;
    border-color: white;
    &:hover, &:focus{
      color: @tzpp_color;
      background: #fff;
    }
  }
}
.btn-primary{
  background: @tzpp_global-color;
  border-color: @tzpp_global-color;
  &:hover, &:focus{
    background: darken(@tzpp_global-color, 5%);
    border-color: darken(@tzpp_global-color, 5%);
  }
}