.module{
  margin-bottom: 40px;
  a{
    color: @tzpp_link-color;
    &.btn{
      color: #fff;
    }
  }
}
.module-ct{
  > ul{
    padding-left: 0;
  }
}
.module-title{
  color: @tzpp_color;
  font-size: 18px;
  line-height: 26px;
  margin-top: 0;
  margin-bottom: 25px;
  &:after{
    content: "";
    display: block;
    height: 1px;
    width: 40px;
    margin-top: 9px;
    background: @tzpp_global-color;
  }
}
.archive-module{
  li{
    padding: 10px 0;
    list-style: none;
    font-weight: 300;
    border-bottom: 1px solid @tzpp_border-color;
    &:first-child{
      padding-top: 0;
    }
    &:last-child{
      padding-bottom: 0;
      border-bottom: none;
    }
  }
}

.mod_tz_tag{
  margin-top: 30px;
  li{
    display: inline-block;
    a{
      color: #888;
      padding: 5px 10px;
      display: inline-block;
      margin: 0 3px 10px 0;
      border: 1px solid fade(#aaaaaa, 30%);
      -webkit-border-radius: 3px;
      -moz-border-radius: 3px;
      border-radius: 3px;
      line-height: 18px;
      .transition(all 0.3 ease-in-out 0s);
      &:hover, &:focus, &:active{
        color: white;
        background: @tzpp_global-color;
        border-color: @tzpp_global-color;
      }
    }
  }
}

.category-menu{
  > div{
    padding: 10px 0;
    list-style: none;
    font-weight: 300;
    border-bottom: 1px solid @tzpp_border-color;
    &:first-child{
      padding-top: 0;
    }
    &:last-child{
      border-bottom: none;
      padding-bottom: 0;
    }
  }
  .sub-menu-category{
    padding-left: 25px;
    > div{
      padding: 10px 0;
      list-style: none;
      font-weight: 300;
      border-bottom: 1px solid @tzpp_border-color;
      &:last-child{
        border-bottom: none;
      }
    }
  }
  [data-toggle="collapse"]{
    float: right;
    color: @tzpp_global-color;
    &.collapsed{
      > .tz_button{
        padding-left: 2px;
        color: @tzpp_link-color;
        border: 1px solid @tzpp_link-color;
      }
    }
    > .tz_button{
      top: -2px;
      width: 14px;
      height: 14px;
      font-size: 7px;
      padding-left: 1px;
      line-height: 12px;
      position: relative;
      text-align: center;
      -webkit-border-radius: 100%;
      -moz-border-radius: 100%;
      border-radius: 100%;
      color: @tzpp_global-color;
      border: 1px solid @tzpp_global-color;
    }
  }
  a.active{
    color: @tzpp_global-color;
  }
}

.tz_portfolio_plus_articles{
  li{
    list-style: none;
    margin-bottom: 15px;
    border-bottom: 1px solid @tzpp_border-color;
    &:last-child{
      border-bottom: none;
    }
  }
  h3{
    a{
      color: @tzpp_color;
      font-size: 14px;
      line-height: 22px;
    }
  }
  .tzpp_media{
    float: left;
    margin-right: 15px;
    img{
      max-width: 80px;
    }
  }
  .info{
    color: @tzpp_gray-color;
    font-size: 12px;
    line-height: 16px;
    font-weight: 400;
    font-style: italic;
    margin-bottom: 5px;
  }
  .title{
    margin-top: 0;
    font-size: 14px;
    line-height: 22px;
  }
}
.breadcrumb{
  .heading-wrapper &{
    color: white;
    text-align: center;
    background: none;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
    text-transform: uppercase;
    a{
      color: white;
    }
    > .active{
      color: white;
    }
    .divider{
      display: none;
    }
    > li{
      &.title{
        display: block;
        + li:before{
          content: "";
          padding: 0;
        }
        h1{
          font-size: 36px;
          font-weight: 700;
          letter-spacing: 5px;
          line-height: 42px;
          margin-top: 0;
          margin-bottom: 20px;
        }
      }
      + li::before{
        color: white;
      }
    }
  }
}

.bottom-2-wrapper{
  font-weight: 300;
  line-height: 24px;
  color: @tzpp_link-color;
  .module-title{
    color: #fff;
  }
  .about{
    margin-bottom: 40px;
    p{
      margin-bottom: 15px;
    }
  }
  .about-image{
    margin-bottom: 25px;
  }
  .social{
    margin-top: 34px;
    ul{
      padding: 0;
    }
    li{
      list-style: none;
      display: inline-block;
      a{
        width: 30px;
        height: 30px;
        color: white;
        padding-top: 1px;
        padding-left: 5px;
        line-height: 28px;
        text-align: center;
        margin-right: 10px;
        .border-radius(3px);
        display: inline-block;
        .transition(all 0.3s linear 0s);
      }
      .facebook{
        background: @fbBackground;
        &:hover, &:focus{
          background: lighten(@fbBackground, 15%);
        }
      }
      .google-plus{
        background: @googlePlusBackground;
        &:hover, &:focus{
          background: lighten(@googlePlusBackground, 15%);
        }
      }
      .twitter{
        background: @twitterBackground;
        &:hover, &:focus{
          background: lighten(@twitterBackground, 15%);
        }
      }
      .dribbble{
        background: @dribbbleBackground;
        &:hover, &:focus{
          background: lighten(@dribbbleBackground, 15%);
        }
      }
      .youtube{
        background: @youtubeBackground;
        &:hover, &:focus{
          background: lighten(@youtubeBackground, 15%);
        }
      }
      .github{
        background: @githubBackground;
        &:hover, &:focus{
          background: lighten(@githubBackground, 15%);
        }
      }
    }
  }

  // Styles for link module
  .link{
    display: table;
    width: 100%;
  }
  .list-link{
    padding: 0;
    margin-bottom: 0;
    a{
      color: @tzpp_link-color;
      &:hover, &:focus{
        color: white;
        text-decoration: none;
      }
    }
    > li{
      list-style: none;
      padding-bottom: 10px;
      &:last-child{
        padding-bottom: 0;
      }
    }
    [class^="linear-icon-"], [class*=" linear-icon-"]{
      color: white;
      font-size: 12px;
      margin-right: 10px;
    }
  }

