  // Styles for news letter module
  .subcrible-content{
    .mce_inline_error,
    #mce-error-response{
      color: @tzpp_global-color;
    }
    #mce-success-response{
      color: white;
    }
    .mc-field-group{
      width: 50%;
      padding: 0 3px;
      display: inline-block;
      &:nth-child(1){
        margin-left: -3px;
      }
      &:nth-child(2){
        right: -6px;
        position: relative;
      }
      &:nth-child(3){
        padding: 0;
        width: auto;
        display: block;
      }
    }
    .subscribe {
      padding-top: 10px;

      input{
        &.inputbox {
          width: 100%;
          height: 40px;
          line-height: 40px;
          background: fade(white, 10%);
          border-radius: 3px;
          display: block;
          margin-bottom: 10px;
          border: none;
          color: #aaa;
          padding: 0 15px;
        }

        &#mc-embedded-subscribe {
          display: block;
          width: 100%;
          height: 40px;
          color: #fff;
          border-radius: 5px;
          text-transform: uppercase;
          text-align: center;
          line-height: 40px;
          margin-top: 20px;
          padding: 0;
          background: #334c9d;
          border: none;
          &:hover, &:focus{
            background: lighten(#334c9d, 15%);
          }
        }
      }
    }
  }
}

.footer-wrapper{
  color: @tzpp_link-color;
  .menu{
    font-size: 11px;
    text-align: right;
    font-weight: 300;
    text-transform: uppercase;
    &.nav > li > a{
      padding: 0 0 0 35px;
    }
    li{
      list-style: none;
      display: inline-block;
      &.open a{
        background: none;
      }
      a{
        &:hover, &:focus, &:active{
          background: none;
        }
      }
    }
  }
}

/* Style for heading */
.heading-content{
  color: #fff;
  text-align: center;
  h1{
    font-size: 60px;
    font-weight: 400;
  }
  p {
    padding: 30px 0 50px;
  }
}
/* End style for heading */

.layout-heading-wrapper{
  .tz-module{
    text-align: center;
  }
  .module-title{
    color: @tzpp_color;
    font-size: 30px;
    line-height: 36px;
    font-weight: 700;
    margin: 0 0 30px;
    padding-bottom: 25px;
    position: relative;
    text-transform: uppercase;
    &:after {
      background: @tzpp_global-color;
      bottom: -25px;
      content: "";
      display: block;
      height: 1px;
      left: 50%;
      margin-top: 5px;
      margin-left: -40px;
      position: relative;
      width: 80px;
    }
  }
}


/* Styles for list layout modules */
.layout-wrapper{
  position: relative;
  &:before {
    content: "";
    display: block;
    margin: auto;
    width: 50px;
    top: 0;
    left: 50%;
    position: absolute;
    margin-left: -25px;
    border-left: 25px solid rgba(0, 0, 0, 0);
    border-right: 25px solid rgba(0, 0, 0, 0);
    border-top: 25px solid #ffffff;
  }
  .gallery_item{
    padding: 30px;
  }
  .item{
    background: #f5f5f5;
    .border-radius(0 0 7px 7px);
    position: relative;
    text-align: center;
    .box-shadow(0 5px 0 rgba(0, 0, 0, 0.1));
    .transition(all 0.3s linear 0s);
    h3{
      .border-radius(0 0 7px 7px);
      color: @tzpp_color;
      font-weight: 400;
      margin: 0;
      padding: 25px 15px;
      .transition(all 0.3s linear 0s);
    }
    img{
      display: block;
      width: 100%;
    }
    &:hover, &:focus{
      .box-shadow(0 5px 0 #3a76af);
      h3{
        background: @tzpp_global-color;
        color: #ffffff;
      }
    }
  }

  &.layout-2-wrapper{
    .gallery_item{
      padding: 20px;
    }
    .item{
      background: #fff;
      > a{
        display: block;
        //height: 270px;
        padding: 10px;
        border: 1px solid @tzpp_border-color;
      }
      h3{
        font-size: 18px;
        padding: 20px 15px;
        border: 1px solid @tzpp_border-color;
        border-top: none;
        border-bottom: none;
      }
      &:hover, &:focus{
        h3{
          border-color: @tzpp_global-color;
        }
      }
    }
  }
}

/* Styles for bottom block */
.buy-now{
  text-align: center;
  h2 {
    color: #ffffff;
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 35px;
    margin-top: 0;
    text-transform: uppercase;
  }
  .btn{
    height: 50px;
    padding: 0 25px;
    font-size: 16px;
    line-height: 50px;
    margin: 0 12px 10px;
    display: inline-block;
    text-transform: uppercase;
  }
  .btn-default{
    background: none;
    border: 1px solid #ffffff;
    .border-radius(5px);
    color: #ffffff;
    overflow: hidden;
    position: relative;
    text-transform: uppercase;
    .transition(all 0.3s linear 0s);
    span{
      font-style: normal;
      position: relative;
      z-index: 1;
    }
    &:hover {
      color: @tzpp_global-color;
      &:after {
        height: 350%;
        opacity: 1;
      }
    }
    &:after {
        background: #fff;
        content: "";
        height: 0;
        left: 50%;
        opacity: 0;
        position: absolute;
        top: 50%;
      z-index: 0;
      -webkit-transform: translateX(-50%) translateY(-50%) rotate(45deg);
      -moz-transform: translateX(-50%) translateY(-50%) rotate(45deg);
      -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
      -o-transform: translateX(-50%) translateY(-50%) rotate(45deg);
      transform: translateX(-50%) translateY(-50%) rotate(45deg);
      .transition(all 0.3s ease 0s);
        width: 100%;
    }
  }
  .btn-primary{
    color: @tzpp_global-color;
    background: #fff;
    border-color: #fff;
    .transition(all 0.3s linear 0s);
    &:hover, &:focus{
      color: #fff;
      background: none;
    }
  }
}
