@media (max-width: 780px) {
  .mega-menu {
    position: static;
    //#plazart-mainnav{
    //  position: static;
    //}
  }
  #tzlogo {
    line-height: 55px;
  }
  #plazart-mainnav{
    .plazart-megamenu{
      a:hover{
        color: @tzpp_global-color;
      }
      display: block;
      text-align: left;
      .navbar-nav > li > a{
        color: @tzpp_gray-color-2;
        padding: 0 13px;
        font-size: 13px;
        line-height: 24px;
        text-transform: none;
      }
      .mega-nav > li,
      .dropdown-menu .mega-nav > li{
        &.active > a{
          color: @tzpp_global-color;
        }
        a{
          font-size: 12px;
          color: @tzpp_gray-color-2;
          &:hover{
            color: @tzpp_global-color;
          }
        }
      }
      .navbar-nav > li > .dropdown-menu ul.mega-nav li {
        border-bottom: 1px solid rgba(170, 170, 170, 0.2);
        &.mega-group{
          border-bottom: none;
        }
      }
      .mega-col-nav{
        padding: 0;
      }
      .navbar-nav > li > .dropdown-menu ul.mega-nav li {
        padding: 0;
      }
      .mega-nav > li a,
      .dropdown-menu .mega-nav > li a{
        padding: 5px 15px;
        &.mega-group-title{
          padding-top: 8px;
        }
      }
    }
    .mega-menu &{
      position: static;
    }
    .navbar-inner{
      .navbar-header button {
        background: rgba(0, 0, 0, 0);
        border: medium none;
        display: block;
        line-height: 55px;
        margin: 0;
        padding: 0;
      }
      .nav-collapse.navbar-collapse {
        background: @tzpp_black-2;
        border-top: 2px solid @tzpp_global-color;
        position: absolute;
        width: 100%;
        left: 0;
        max-height: 480px;
        overflow-y: auto;
        padding: 0;
        z-index: 9999;
        margin-bottom: 0;
        -webkit-box-shadow: 0 2px 3px -3px @tzpp_gray-color-2;
        box-shadow: 0 2px 3px -3px @tzpp_gray-color-2;
        .plazart-megamenu > ul {
          margin: 0;
          width: 100%;
        }
        .plazart-megamenu > ul > li {
          border-bottom: 1px solid rgba(170, 170, 170, 0.2);
          padding: 10px 0;
          &:hover, &.active{
            background: @tzpp_black;
            > a{
              color: @tzpp_global-color;
            }
          }
        }
        .dropdown-menu {
          padding-top: 0;
          margin-top: 10px;
          background: @tzpp_color;
        }
      }
    }
  }

  .bottom-2-wrapper{

  }
}