@media (max-width: 767px) {
  .heading-content{
    h1{
      font-size: 38px;
    }
    p{
      padding: 20px 0 30px;
    }
  }
  body #tz-heading-wrapper{
    padding: 90px 0 100px !important;
  }
  .layout-wrapper{
    .gallery_item {
      padding: 20px 15px;
      &:first-child{
        padding-top: 0;
      }
      &:last-child{
        padding-bottom: 0;
      }
    }
    &.layout-2-wrapper .gallery_item{
      padding: 20px 15px;
    }
  }
  .buy-now .btn{
    margin-right: 0;
  }
  .TzItemPage{
    #comments{
      .comment-avatar{
        //float: none;
        padding-right: 10px;
      }
      .avatar-indent{
        padding-left: 0;
      }
      .comments-list{
        margin-left: 0;
      }
    }
  }
}
@media (max-width: 480px) {
  .footer-wrapper
  {
    text-align: center;
    .menu{
      text-align: center;
    }
    .menu.nav > li > a {
      padding: 0 10px;
    }
  }
}
@media (max-width: 479px) {
  .bottom-2-wrapper .list-link {
    float: none !important;
  }
}