body{
  line-height: 24px;
}
a{
  color: @tzpp_link-color;
  &:hover, &:focus, &:active{
    outline: none;
    text-decoration: none;
    color: @tzpp_global-color;
  }
}
img{
  max-width: 100%;
}
#tzlogo{
  line-height: 80px;
  img{
    max-width: 100%;
    vertical-align: middle;
  }
}
#tz-menu-wrapper {
  #plazart-mainnav {
    margin: 0;
    border: none;
    .navbar-collapse {
      padding: 0;
    }
  }
  border-top: 1px solid @plazart-border-color;
  border-bottom: 1px solid @plazart-border-color;
}

.tz-module {
  .nav > {
    li > {
      a {
        display: block;
        padding: 5px 10px;
      }
    }
  }
}
.form-group {
  margin-bottom: 20px;
}

#login-form ul.unstyled {
  margin-top: 20px;
  padding-left: 20px;
}

.framework-logo {
  padding: 10px 0 30px;
}